/* cairo-regular - latin */
@font-face {
  font-family: 'Cairo';
  font-style: normal;
  font-weight: 400;
  src: url('/font/cairo/cairo-v6-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Cairo'), local('Cairo-Regular'),
       url('/font/cairo/cairo-v6-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/font/cairo/cairo-v6-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('/font/cairo/cairo-v6-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('/font/cairo/cairo-v6-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('/font/cairo/cairo-v6-latin-regular.svg#Cairo') format('svg'); /* Legacy iOS */
}
/* cairo-700 - latin */
@font-face {
  font-family: 'Cairo';
  font-style: normal;
  font-weight: 700;
  src: url('/font/cairo/cairo-v6-latin-700.eot'); /* IE9 Compat Modes */
  src: local('Cairo Bold'), local('Cairo-Bold'),
       url('/font/cairo/cairo-v6-latin-700.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/font/cairo/cairo-v6-latin-700.woff2') format('woff2'), /* Super Modern Browsers */
       url('/font/cairo/cairo-v6-latin-700.woff') format('woff'), /* Modern Browsers */
       url('/font/cairo/cairo-v6-latin-700.ttf') format('truetype'), /* Safari, Android, iOS */
       url('/font/cairo/cairo-v6-latin-700.svg#Cairo') format('svg'); /* Legacy iOS */
}

/* cyrillic */
@font-face {
  font-family: 'Anonymous Pro';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Anonymous Pro Regular'), local('AnonymousPro-Regular'), url('/font/Anonymous_Pro/AnonymousePro-Regular.woff2') format('woff2');
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
}
